<template>
  <div id="editare-film">
    <h3>Editare Categorie</h3>
    <div class="row">
      <form @submit.prevent="editeazaCategorie()" class="col s8">
        <div class="row">
          <div class="input-field col s8">
            <input type="text" v-model="denumireCategorie" required />
          </div>
        </div>

        <button type="submit" class="btn">Save</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import appService from "./api.service.js";
export default {
  name: "editare-categorie",
  data() {
    return {
      denumireCategorie: null
    };
  },
  created() {
    appService.viewOneCategorie(this.$route.params.categorie_id).then(data => {
      this.denumireCategorie = this.$route.params.categorie_id;
    });
  },
  methods: {
    editeazaCategorie() {
      if (confirm("Sunteti sigur ?")) {
        appService.updateCategorie(this.$route.params.categorie_id);
        this.$router.push("/");
      }
    }
  }
};
</script>



