import axios from 'axios'

axios.defaults.baseURL = "https://server-tic.herokuapp.com"

const appService = {
    getCategorii(){
        return new Promise((resolve => {
            axios.get('/categorie/afisare')
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    getFilme(gen){
        return new Promise((resolve => {
            axios.get(`/film/afisare?gen=${gen}`)
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    getFilm(gen, id){
        return new Promise((resolve => {
            axios.get(`/film/afisareDetaliiFilm?gen=${gen}&id=${id}`)
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    deleteFilm(gen,id){
        return new Promise((resolve => {
            axios.delete(`/film/sterge?gen=${gen}&id=${id}`)
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    addFilm(t, du, a, r, d, g){
        return new Promise((resolve => {
            axios.post('/film/adauga', {
                titlu: t,
                durata: du,
                anul_aparitie: a,
                regia: r,
                distribuitor: d,
                gen: g
            })
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    updateFilm(t, du, a, r, d, gen, id){
        return new Promise((resolve => {
            axios.put(`/film/modifica?gen=${gen}&id=${id}`, {
                titlu: t,
                durata: du,
                anul_aparitie: a,
                regia: r,
                distribuitor: d,
                gen: gen
            })
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    addCategorie(den, nr){
        return new Promise((resolve => {
            axios.post('/categorie/adauga', {
                denumireCategorie: den,
                numar_filme: nr
            })
            .then(response => {
                resolve(response.data)
            })
        }))
    },
    
    deleteCategorie(den){
        return new Promise((resolve => {
            axios.delete(`/categorie/sterge?denumireCategorie=${den}`)
            .then(response => {
                resolve(response.data)
            })
        }))
    },

    updateCategorie(gen){
        return new Promise((resolve => {
            axios.put(`/categorie/modifica?gen=${gen}`, {
                denumireCategorie: gen
            })
            .then(response => {
                resolve(response.data)
            })
            .catch(err => {
                console.log(err)
            })
        }))
    },

    viewOneCategorie(den){
        return new Promise((resolve => {
            axios.get(`/categorie/afisareCategorie?denumireCategorie=${den}`)
            .then(response => {
                resolve(response.data)
            })
        }))
    }
}

export default appService


