<template>
  <div id="dashboard">
    <ul class="collection with-header">
      <li class="collection-header"><h4>Categorii</h4></li>
      <li
        v-for="categorie in categorii"
        v-bind:key="categorie.numeCategorie"
        class="collection-item"
      >
        {{ categorie.numeCategorie }}~{{ categorie.numar_filme }}
    
        <router-link
          class="secondary-content"
          v-bind:to="{
            name: 'detalii-categorie',
            params: { categorie_id: categorie.numeCategorie }
          }"
        >
          <i class="fa fa-eye"></i>
        </router-link>
      </li>
    </ul>

    <button id="btnCresc" @click="sortCresc()" class="btn blue">
      <i class="material-icons">arrow_upward</i>
    </button>
    <button id="btnDesc" @click="sortDesc()" class="btn pink">
      <i class="material-icons">arrow_downward</i></button>

    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{ name: 'categorie-noua' }"
        class="btn-floating btn-large orange"
      >
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import appService from "./api.service.js";
export default {
  name: "dashboard",
  data() {
    return {
      categorii: []
    };
  },
  created() {
    appService.getCategorii().then(data => {
      this.categorii = data;
    });
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.updateDate();
      next();
    });
  },
  methods: {
    updateDate() {
      appService.getCategorii().then(data => {
        this.categorii = data;
      });
    },

    sortDesc() {
      this.categorii.sort((a, b) => (a.numar_filme < b.numar_filme ? 1 : -1));
    },
    sortCresc() {
      this.categorii.sort((a, b) => (a.numar_filme > b.numar_filme ? 1 : -1));
    }
  }
};
</script>



