<template>
  <div id="detalii-film">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{ titlu }}</h4>
      </li>
      <li class="collection-item">Durata: {{ durata }}</li>
      <li class="collection-item">Anul aparitiei: {{ anul_aparitie }}</li>
      <li class="collection-item">Regia: {{ regia }}</li>
      <li class="collection-item">Distribuitor: {{ distribuitor }}</li>
      <li class="collection-item">Gen: {{ gen }}</li>
    </ul>
    <router-link
      v-bind:to="{
        name: 'detalii-categorie',
        params: { categorie_id: this.$route.params.categorie_id }
      }"
      class="btn grey"
      >Back</router-link
    >
    <button @click="stergeFilm" class="btn red">Delete</button>

    <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'editare-film', params: {film_id: this.$route.params.film_id}}" class="btn-floating btn-large orange">
                <i class="fa fa-pencil"></i>
            </router-link>
        </div>
  </div>
</template>

<script>
import appService from "./api.service.js";
export default {
  name: "detalii-film",
  data() {
    return {
      film_id: null,
      titlu: null,
      durata: null,
      anul_aparitie: null,
      regia: null,
      distribuitor: null,
      gen: null
    };
  },
  created() {
    appService
      .getFilm(this.$route.params.categorie_id, this.$route.params.film_id)
      .then(data => {
        this.film_id = data.film_id;
        this.titlu = data.titlu;
        this.durata = data.durata;
        this.anul_aparitie = data.anul_aparitie;
        this.regia = data.regia;
        this.distribuitor = data.distribuitor;
        this.gen = data.gen;
      });
  },
  methods: {
    stergeFilm() {
      if (confirm("Sunteti sigur ?")) {
        appService.deleteFilm(this.$route.params.categorie_id, this.$route.params.film_id);
        this.$router.push(`/${this.$route.params.categorie_id}`)
      }
    }
  }
};
</script>



