<template>
  <div id="editare-film">
    <h3>Editare Film</h3>
    <div class="row">
      <form @submit.prevent="modificaFilm" class="col s8">
        <div class="row">
          <div class="input-field col s8">
            <input type="text" v-model="titlu" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s8">
            <input type="text" v-model="durata" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s8">
            <input type="text" v-model="anul_aparitie" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s8">
            <input type="text" v-model="regia" required />
          </div>
        </div>
        <div class="row">
          <div class="input-field col s8">
            <input type="text" v-model="distribuitor" required />
          </div>
        </div>
        <button type="submit" class="btn">Save</button>
        <router-link to="/" class="btn grey">Cancel</router-link>
      </form>
    </div>
  </div>
</template>

<script>
import appService from "./api.service.js";
export default {
  name: "editare-film",
  data() {
    return {
      titlu: null,
      durata: null,
      anul_aparitie: null,
      regia: null,
      distribuitor: null,
      gen: null
    };
  },
  created() {
    appService
      .getFilm(this.$route.params.categorie_id, this.$route.params.film_id)
      .then(data => {
        this.titlu = data.titlu;
        this.durata = data.durata;
        this.anul_aparitie = data.anul_aparitie;
        this.regia = data.regia;
        this.distribuitor = data.distribuitor;
        this.gen = data.gen;
      });
  }, 
  methods: {
      modificaFilm(){
          if (confirm("Sunteti sigur ?")) {
        appService.updateFilm(this.titlu, this.durata, this.anul_aparitie, this.regia, this.distribuitor, this.$route.params.categorie_id, this.$route.params.film_id);
        this.$router.push(`/${this.$route.params.categorie_id}`)
      }
      }
  }
};
</script>



