<template>
    <div id="categorie-noua">
        <h3>Categorie Noua</h3>
        <div class="row">
            <form @submit.prevent="adaugaCategorie" class="col s8">
                <div class="row">
                    <div class="input-field col s8">
                        <input type="text" v-model="denumire" required>
                        <label>Denumire Categorie</label>
                    </div>
                </div>
                
                <button type="submit" class="btn">Save</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import appService from './api.service.js'
export default {
    name: 'categorie-noua',
    data() {
        return {
            denumire: null,
            numar_filme: null
        }
    },
  methods: {
        adaugaCategorie() {
            appService.addCategorie(this.denumire, 0);
            this.$router.push('/')
        }
    }
}
</script>


