<template>
    <div id="film-nou">
        <h3>Film Nou</h3>
        <div class="row">
            <form @submit.prevent="adaugaFilm" class="col s8">
                <div class="row">
                    <div class="input-field col s8">
                        <input type="text" v-model="titlu" required>
                        <label>Titlu</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s8">
                        <input type="text" v-model="durata" required>
                        <label>Durata</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s8">
                        <input type="text" v-model="anul_aparitie" required>
                        <label>Anul aparitiei</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s8">
                        <input type="text" v-model="regia" required>
                        <label>Regia</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s8">
                        <input type="text" v-model="distribuitor" required>
                        <label>Distribuitor</label>
                    </div>
                </div>
                <button type="submit" class="btn">Save</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import appService from './api.service.js'
export default {
    name: 'film-nou',
    data() {
        return {
            titlu: null,
            durata: null,
            anul_aparitie: null,
            regia: null,
            distribuitor: null,
            gen: null
        }
    },
  methods: {
        adaugaFilm() {
            appService.addFilm(this.titlu, this.durata, this.anul_aparitie, this.regia, this.distribuitor, this.$route.params.categorie_id);
            this.$router.push(`/${this.$route.params.categorie_id}`)
        }
    }
}
</script>


