import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import NewFilm from '@/components/NewFilm'
import ViewFilm from '@/components/ViewFilm'
import EditareFilm from '@/components/EditareFilm'
import ViewCategorie from '@/components/ViewCategorie'
import NewCategorie from '@/components/NewCategorie'
import EditareCategorie from '@/components/EditareCategorie'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard
    },
    {
      path: '/:categorie_id/new',
      name: 'film-nou',
      component: NewFilm
    },
    {
      path: '/:categorie_id/:film_id/edit',
      name: 'editare-film',
      component: EditareFilm
    },
    {
      path: '/:categorie_id/:film_id',
      name: 'detalii-film',
      component: ViewFilm
    },
    {
      path: '/:categorie_id',
      name: 'detalii-categorie',
      component: ViewCategorie
    },
    {
      path: '/new',
      name: 'categorie-noua',
      component: NewCategorie
    },
    {
      path: '/:categorie_id/edit',
      name: 'editare-categorie',
      component: EditareCategorie
    },
  ]
})



