<template>
  <div id="detalii-categorie">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4 id="gen">{{ this.$route.params.categorie_id }}</h4>
      </li>
      <li
        v-for="titluFilm in titluriFilme"
        v-bind:key="titluFilm.titlu"
        class="collection-item"
      >
        {{ titluFilm.titlu }}~{{ titluFilm.durata }}
        <router-link
          class="secondary-content"
          v-bind:to="{
            name: 'detalii-film',
            params: { film_id: titluFilm.id }
          }"
        >
          <i class="material-icons">movie_filter</i>
        </router-link>
      </li>
    </ul>
    <router-link v-bind:to="{ name: 'dashboard' }" class="btn grey"
      >Back</router-link
    >
    <button @click="stergeCategorie" class="btn red">Delete</button>

    <div class="secondary-content">
        <button id="btnCresc" @click="sortCresc()" class="btn blue">
          <i class="material-icons">arrow_upward</i>
        </button>
        <button id="btnDesc" @click="sortDesc()" class="btn pink">
          <i class="material-icons">arrow_downward</i>
        </button>
    </div>

    <div class="fixed-action-btn">
      <router-link
        v-bind:to="{
          name: 'film-nou',
          params: { categorie_id: this.$route.params.categorie_id }
        }"
        class="btn-floating btn-large orange"
      >
        <i class="fa fa-plus"></i>
      </router-link>
    </div>
  </div>
</template>

<script>
import appService from "./api.service.js";
export default {
  name: "detalii-categorie",
  data() {
    return {
      titluriFilme: []
    };
  },
  created() {
    appService.getFilme(this.$route.params.categorie_id).then(data => {
      this.titluriFilme = data;
    });
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.updateDate();
      next();
    });
  },
  methods: {
    stergeCategorie() {
      console.log(this.$route.params.categorie_id);
      if (confirm("Sunteti sigur ?")) {
        appService.deleteCategorie(this.$route.params.categorie_id);
        this.$router.push("/");
      }
    },
    updateDate() {
      appService.getFilme(this.$route.params.categorie_id).then(data => {
        this.titluriFilme = data;
      });
    },
    sortDesc() {
      this.titluriFilme.sort((a, b) => (a.titlu < b.titlu ? 1 : -1));
    },
    sortCresc() {
      this.titluriFilme.sort((a, b) => (a.titlu > b.titlu ? 1 : -1));
    }
  }
};
</script>

<style>
#gen {
  text-transform: uppercase;
}
</style>



